<template>
    <div v-loading="loading" style="min-height:300px;" :key="pageObj.indentity">
        <blog-container :url="url"></blog-container>
    </div>
</template>

<script>
import blogContainer from '../layout/container.vue';
import { getTimeStamp } from '../../utils/StringUtils';
export default {
    name:"common-blog-article-container",
    components:{
        blogContainer
    },
    data(){
        return {
            data:null,
            loading:true,
            url:''
        }
    },
    computed: {
        pageObj(){
            this.getfilter(this.$store.state.pageObj);
            return this.$store.state.pageObj;
        },
    },
     created() {
         this.getData();
     },
     methods: {
         getData(){
             /**获取数据 */
           let hash = window.location.hash.replace('#','').trim();
           let version = getTimeStamp();
           let _this =this;
           if(this.data !=null)
                this.data.splice(0,this.data.length);
           fetch('db/articles/articles.json?v='+version)
                .then(function(response) {
                    return response.json();
                })
                .then(function(data) {
                      _this.data=data;
                      //默认第1个
                      data.filter(item=>{
                          if(item.category === hash){
                              if(item.children !=null){
                                  _this.url = item.children[0].url;
                              }
                          }
                      })
                });
         },
         getfilter(obj){
             /**获取过滤 */
            let hash = window.location.hash.replace('#','').trim();
             if(this.data !=null ){
                 let category =null;
                 this.data.filter(item=>{
                      if(item.category === obj.category && item.category === hash){
                            category = item.children;
                            return true;
                      }else{
                          return false;
                      }
                 });
                 if(category !=null){
                     let cur_Context_url ="";
                     category.filter(item=>{
                         if(item.id === obj.indentity){
                             cur_Context_url = item.url;
                             return true;
                         }else{
                             return false;
                         }
                     });
                     this.url = cur_Context_url;
                     
                 }
             }
             this.loading=false;
         }
     }
}
</script>

